import { Collapse,Select,Card,Layout, Table, Tag, Space,Popover, List, Button,Typography, Divider,Statistic, Affix } from 'antd';
import 'antd/dist/antd.css';
import { CaretRightOutlined } from '@ant-design/icons';
import './App.css';
import rawSkillList from './result_upd.json'
import wujiangList from './wujiangList.json'
import React from 'react';


const {Header, Content, Footer} =Layout;
const {Option}=Select;
const { Panel } = Collapse;
const { Title, Paragraph, Text, Link } = Typography;

var skillList=[...rawSkillList];
var color_map={
    '': "red",
    '': "volcano",
    '': "orange",
    '':"cyan",
    '': "purple",
    '': "blue",
    '':"lime",
    '':"gold",
    '':"purple"
  };

var tagFilter=[];
var tagSelector=[];
for (var key in color_map){
    let element={text:<Tag color={color_map[key]}>{key} </Tag>, value:key};
    tagSelector.push(<Tag color={color_map[key]}>{key} </Tag>);
    tagFilter.push(element);}
    
var skillLists=[];
var recoverList=[];
var to_delete=[];
var skillsOnAttention=['',''];
skillLists.push(skillList.filter(item => item['']===0).sort(function(a,b){return a[''].localeCompare(b[''])}));
skillLists.push(skillList.filter(item => item['']===1).sort(function(a,b){return a[''].localeCompare(b[''])}));
skillLists.push(skillList.filter(item => item['']===2).sort(function(a,b){return a[''].localeCompare(b[''])}));

function update_to_delete(values){
    to_delete=[];
    for (let i=0;i<values.length;i++){
        for (let j=0;j<wujiangList[values[i]][''].length;j++){
            to_delete.push(wujiangList[values[i]][''][j]);
        }
    }
}
class App extends React.Component{
    constructor(){
        super();
        this.state={
            'skillList0': skillLists[0],
            'skillList0Length':skillLists[0].length,
            'skillList1': skillLists[1],
            'skillList1Length':skillLists[1].length,
            'skillList2': skillLists[2],
            'skillList2Length':skillLists[2].length,
            'skillsOnAttention':skillsOnAttention
        }
        this.del_skill=this.del_skill.bind(this);
        this.upd_state=this.upd_state.bind(this);
        this.resetList=this.resetList.bind(this);
        this.recover=this.recover.bind(this);
        this.resetPool=this.resetPool.bind(this);
        this.add_skill_onattention=this.add_skill_onattention.bind(this);
        this.columns0 = [
            {
              title:  () => { 
                return <div className='my-table-header'>({this.state['skillList0'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text[''].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['']}</Popover></div>,
              sorter: (a, b) => a[''].localeCompare(b['']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record[''].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,0)}, //  
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
          this.columns1 = [
            {
              title:  () => { 
                return <div className='my-table-header'>({this.state['skillList1'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text[''].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['']}</Popover></div>,
              sorter: (a, b) => a[''].localeCompare(b['']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record[''].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,1)}, //  
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
          this.columns2 = [
            {
              title:  () => { 
                return <div className='my-table-header'>({this.state['skillList2'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text[''].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['']}</Popover></div>,
              sorter: (a, b) => a[''].localeCompare(b['']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record[''].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,2)}, //  
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
    }
    
    upd_state(){
        this.setState({'skillList0': skillLists[0],
        'skillList0Length':skillLists[0].length,
        'skillList1': skillLists[1],
        'skillList1Length':skillLists[1].length,
        'skillList2': skillLists[2],
        'skillList2Length':skillLists[2].length,});
    }
    del_skill(val, skillListID){
        skillLists[skillListID] = skillLists[skillListID].filter(function(x) { return x[''] !== val[''] });
        recoverList.push(val['']);
        this.upd_state();
    }
    add_skill_onattention(value){
        skillsOnAttention=[];
        for (let v in value){
            skillsOnAttention.push(value[v]);
        }
        this.setState({'skillsOnAttention':skillsOnAttention});

    }
    resetList(){
        skillLists=[];
        skillLists.push(skillList.filter(item => item['']===0).sort(function(a,b){return a[''].localeCompare(b[''])}));
        skillLists.push(skillList.filter(item => item['']===1).sort(function(a,b){return a[''].localeCompare(b[''])}));
        skillLists.push(skillList.filter(item => item['']===2).sort(function(a,b){return a[''].localeCompare(b[''])}));
        this.upd_state();
    }
    recover(){
        if(recoverList.length>0){
            let t=skillList.filter(item=> item['']===recoverList[recoverList.length-1]);
            skillLists[t[0]['']].push(t[0]);
            this.upd_state();
            recoverList.pop();
        }
    }

    resetPool(){
        skillList=[...rawSkillList];
        for(let i=0;i<to_delete.length;i++){
            skillList=skillList.filter(item => item['']!==to_delete[i]);
        }
        this.resetList();
    }
    render(){
        return(<Layout>
        <Header><div className='App-header'></div></Header>
        <Content>
        <Collapse
                bordered={false}
                defaultActiveKey={['3']}
                expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
                className="site-collapse-custom-collapse"
                >
                <Panel header="" key="1" className="site-collapse-custom-panel">
                <Select
                    mode="multiple"
                    allowClear
                    style={{ width: '100%' }}
                    defaultValue={['','']}
                    placeholder="..."
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.children.indexOf(input) >= 0
                        }
                    onChange={this.add_skill_onattention}
                    >
                    {skillList.map((x)=>{
                        
                        return(<Option value={x['']}>{x['']}</Option>)})}
                
            
                </Select>
                <Card size="small" style={{'width': 'fit-content'} }>
                {this.state['skillsOnAttention'].map((x)=>{
                        let rate=-1;
                        for (let i=0;i<skillLists.length;i++){
                            if(skillLists[i].filter(function(y){return (y['']===x)}).length>0){
                                rate=3.0/skillLists[i].length;
                            }
                        }
                        if(rate>0){
                        return (<Card.Grid size="small" style={{'width': 'fit-content'} } > <Statistic
                        title={x}
                        value={100*rate}
                        precision={2}
                        valueStyle={{ color: '#3f8600' }}
                        
                        suffix="%"
                      /></Card.Grid>);}
                        else{
                            return(<Card.Grid size="small" style={{'width': 'fit-content'} } ><Statistic
                            title={x}
                            value=""
                            valueStyle={{ color: '#cf1322' }}
                          /></Card.Grid>);
                        }
                    })}
                </Card>
                </Panel>
                <Panel header=" ()" key="2" className="site-collapse-custom-panel">
    
                {tagSelector}

                </Panel>
                <Panel header="" key="3" className="site-collapse-custom-panel">
                <Select
                    mode="multiple"
                    allowClear
                    style={{ width: '100%' }}
                    defaultValue={[]}
                    placeholder=""
                    optionFilterProp="children"
                    onChange={update_to_delete}
                    filterOption={(input, option) =>
                        option.children.indexOf(input) >= 0
                        }
                    >
                    {wujiangList.map((x)=>{
                        return(<Option value={x['ID']}>{x['']}</Option>);
                    })}
                </Select>
                <Button type='primary' onClick={this.resetPool}></Button>
                </Panel>
        </Collapse>
        
        
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList0']]} columns={this.columns0}  pagination={false}></Table>}</Layout>
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList1']]} columns={this.columns1}  pagination={false}></Table>}</Layout>
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList2']]} columns={this.columns2}  pagination={false}></Table>}</Layout>
        <Layout className="my-table"><Affix offsetTop={120} onChange={(affixed) => console.log(affixed)}>
        <Button onClick={this.resetList}></Button>
        <p><Button onClick={this.recover}></Button></p>
        <p></p>
        </Affix></Layout>
        </Content>
        </Layout>)
    }
}

export default App;